* {
  box-sizing: border-box;
  margin: 0;
}

@font-face {
  font-family: Europa;
  font-weight: normal;
  src: url("europa-regular-webfont.0818becd.woff2") format("woff2");
}

@font-face {
  font-family: Europa bold;
  src: url("europa-bold-webfont.39bc7bcf.woff2") format("woff2");
}

body {
  background-image: url("bg.582ce920.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  min-height: 100vh;
  font-family: Europa;
}

.app {
  height: 100%;
}

.grid {
  background: #fffefe;
  border-radius: 16px;
  width: 550px;
  height: -moz-fit-content;
  height: fit-content;
  padding: 20px 26px;
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  box-shadow: 3px 3px 35px #00000080;
}

.grid > div {
  flex-direction: column;
  gap: 26px;
  display: flex;
}

.grid__area {
  background: #fff;
  border-radius: 10px;
  height: 80%;
  box-shadow: 3px 3px 35px #00000026;
}

.grid__area > div {
  grid-template-columns: repeat(16, 26px);
  gap: 4px;
  width: 100%;
  height: 100%;
  padding: 10px;
  display: grid;
}

.grid__row > div {
  gap: 4px;
  width: 100%;
  display: grid;
}

.grid__row:first-child .grid__cell:first-child {
  border-radius: 6px 2px 2px;
}

.grid__row:first-child .grid__cell:last-child {
  border-radius: 2px 6px 2px 2px;
}

.grid__row:last-child .grid__cell:first-child {
  border-radius: 2px 2px 2px 6px;
}

.grid__row:last-child .grid__cell:last-child {
  border-radius: 2px 2px 6px;
}

.grid__cell {
  background: #e8f1ff;
  border-radius: 2px;
  justify-content: center;
  align-items: center;
  width: 26px;
  height: 26px;
  font-size: .7rem;
  display: flex;
}

.grid .selected {
  color: #fff;
  background-color: #3d7bd9;
}

.grid h1 {
  font-family: Europa bold;
  font-size: 14px;
}

.grid p {
  font-size: 12px;
}

.grid__footer > div {
  justify-content: space-between;
  display: flex;
}

.grid__footer > div p {
  color: #00000080;
  margin-bottom: 0;
}

.grid__footer > div .selection_coordinates, .grid__footer > div .selection_total {
  color: #00000080;
}

.grid__footer > div .selection_coordinates .value, .grid__footer > div .selection_total .value {
  color: #000;
  padding-left: 5px;
}

